<template>
    <div class="container-fluid">
        <div class="row justify-content-center">
            <div class="col-md-3 p-0 z-2">
                <Control :address='address' @setAddress='getAddress'/>
            </div>
            <div class="col-md-9 p-0">
                <Map :address='address' />
            </div>
        </div>
    </div>
</template>

<script>

import Map from '../components/Map'
import Control from '../components/Control'
export default {
    components: {
        Map,
        Control
    },
    data(){
        return{
            address:'台南市'
        }
    },
    methods: {
        getAddress(item){
            this.address = item;
        }
    }
}
</script>