<template>
  <div id="map"></div>
</template>

<script>
import mask from "../assets/img/tattoos.png";
import maskYellow from "../assets/img/tattoos-yellow.png";
import maskRed from "../assets/img/tattoos-red.png";
import close from "../assets/img/close.png";
export default {
  props:['address'],
  data() {
    return {
      map: "",
      geocoder: '',
      masks: [],
      maskData: [],
      maskMarker: [],
    };
  },
  methods: {
    getMask() {
      const api = `https://raw.githubusercontent.com/kiang/pharmacies/master/json/points.json`;
      const vm = this;
      this.$http.get(api).then(response => {
        vm.masks = response.data.features;
        vm.masks.forEach(item => {
          var isIcon;
          if (item.properties.mask_adult >= 50) {
            isIcon = mask;
          } else if (
            item.properties.mask_adult < 50 &&
            item.properties.mask_adult > 20
          ) {
            isIcon = maskYellow;
          } else if (
            item.properties.mask_adult > 0 &&
            item.properties.mask_adult <= 20
          ) {
            isIcon = maskRed;
          } else {
            isIcon = close;
          }
          vm.maskData.push({
            lat: item.geometry.coordinates[0],
            lng: item.geometry.coordinates[1],
            icon: isIcon
          });
        });
        for (let i = 0; i < vm.maskData.length; i++) {
          vm.maskMarker[i] = new google.maps.Marker({
            position: {
              lat: vm.maskData[i].lng,
              lng: vm.maskData[i].lat
            },
            map: vm.map,
            icon: vm.maskData[i].icon
          });
        }
      });
    },
    initMap() {
      this.geocoder = new google.maps.Geocoder();
      this.map = new google.maps.Map(document.getElementById("map"), {
        // center: { lat: 22.944308, lng: 120.259945 },
        zoom: 16
      });

      this.geocoder.geocode({'address': this.address}, (results, status) => {
          if(status == 'OK'){
              this.map.setCenter(results[0].geometry.location);
              const marker = new google.maps.Marker({
                  map: this.map,
                  position: results[0].geometry.location,
              })
          }else{
              alert(123)
          }
      })
    },
  },
  watch:{
    address: function(){
        this.initMap();
        this.getMask();
    }
  },
  mounted() {
    this.initMap();
    this.getMask();
  }
};
</script>

